(function() {
  var indexOf = [].indexOf || function(item) { for (var i = 0, l = this.length; i < l; i++) { if (i in this && this[i] === item) return i; } return -1; };

  app.behaviors.translating = {
    properties: {
      LANG: {
        type: String,
        notify: true,
        value: app.lang.defaultLanguage
      },
      supportedLanguageList: {
        type: Array,
        value: []
      }
    },
    created: function() {
      return this.supportedLanguageList = app.lang.supportedLanguageList;
    },
    setActiveLanguage: function(languageIdentifier) {
      if (indexOf.call(this.supportedLanguageList, languageIdentifier) < 0) {
        return;
      }
      if (this.LANG !== languageIdentifier) {
        this.set('LANG', languageIdentifier);
        app.lang.defaultLanguage = languageIdentifier;
        app.lang.saveDefaultLanguage();
        return window.location.reload();
      }
    },
    $TRANSLATE_NUMBER: function(number, languageIdentifier) {
      return app.lang.translateNumeral(number, languageIdentifier);
    },
    $TRANSLATE: function(phraseIndentifier, languageIdentifier) {
      return app.lang.translatePhrase(phraseIndentifier, languageIdentifier);
    }
  };

}).call(this);
